html {
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;

}

p, 
h1, h2, h3, h4, h5, h6,
ul {
    padding: 0;
    margin: 0;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

button:active, 
button:focus {
    outline: none;
}


